import { createWebHistory, createRouter } from "vue-router";

import pageFilm from "@/views/page-film.vue";
import pageCinema from "@/views/page-cinema.vue";
import pageInformation from "@/views/page-information.vue";
import pageMypage from "@/views/page-mypage.vue";

import ongoing from "@/components/public_module/module-ongoing.vue";
import beAboutTo from "@/components/public_module/module-beAboutTo.vue";
import filmInfo from "@/components/show_page/page-filmInfo.vue";

const routes = [
  {
    path: "/",
    name: "Film",
    component: pageFilm,
    redirect: "/Film/ongoing",
    children: [
      {
        path: "/Film/ongoing",
        name: "ongoing",
        component: ongoing,
      },
      {
        path: "/Film/beAboutTo",
        name: "beAboutTo",
        component: beAboutTo,
      },
    ],
  },

  {
    path: "/Cinema",
    name: "Cinema",
    component: pageCinema,
  },

  {
    path: "/Information",
    name: "Information",
    component: pageInformation,
  },

  {
    path: "/Mypage",
    name: "Mypage",
    component: pageMypage,
  },

  {
    path: "/filmInfo",
    name: "filmInfo",
    component: filmInfo,
  },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;
