<template>
  <LoadingAnimations></LoadingAnimations>
  <div>
    <nav>
      <ul>
        <li>
          <RouterLink to="/">
            <img :src="require(`@/assets/footer/movie-${this.currentPage === 'Film'}.png`)" alt="movie-false"
              width="32px" height="32px">
            <span :style="currentPage === 'Film' ? { color: '#ff5f16' } : { color: '#797d82' }">电影</span>
          </RouterLink>
        </li>
        <li>
          <RouterLink :to="{ name: 'Cinema' }">
            <img :src="require(`@/assets/footer/cinema-${this.currentPage === 'Cinema'}.png`)" alt="cinema-false"
              width="32px" height="32px">
            <span :style="currentPage === 'Cinema' ? { color: '#ff5f16' } : { color: '#797d82' }">影院</span>
          </RouterLink>
        </li>
        <li>
          <RouterLink :to="{ name: 'Information' }">
            <img :src="require(`@/assets/footer/information-false.png`)" alt="information-false" width="32px"
              height="32px">
            <span :style="currentPage === 'Information' ? { color: '#ff5f16' } : { color: '#797d82' }">资讯</span>
          </RouterLink>
        </li>
        <li>
          <RouterLink :to="{ name: 'Mypage' }">
            <img :src="require(`@/assets/footer/mypage-${this.currentPage === 'Mypage'}.png`)" alt="mypage-false"
              width="32px" height="32px">
            <span :style="currentPage === 'Mypage' ? { color: '#ff5f16' } : { color: '#797d82' }">我的</span>
          </RouterLink>
        </li>
      </ul>
    </nav>
    <main>
      <RouterView />
    </main>
  </div>
</template>

<script>
// 
import LoadingAnimations from '@/components/public_module/Loading-animations.vue';

export default {
  components: { LoadingAnimations },
  beforeMount() {// 挂载前
    // --
    this.property.winInformation.winWidth = window.innerWidth;
    this.property.winInformation.winHeight = window.innerHeight;
    // 监听视窗宽高
    window.addEventListener('resize', () => {
      this.property.winInformation.winWidth = window.innerWidth;
      this.property.winInformation.winHeight = window.innerHeight;
    })
  },
  mounted() {// 挂载后

  },
  data() {
    return {
      currentPage: "Film",
      property: {
        requestGetInfo: {
          filmsList: [],
          beAboutToFilmsList: []
        },
        winInformation: {
          winWidth: 0,
          winHeight: 0
        },
      },
    }
  },
  methods: {

  },
  watch: {
    'property.winInformation.winWidth': {
      immediate: true,
      handler(value) {
        document.documentElement.style.setProperty('--winWidth', value);
      }
    },
    'property.winInformation.winHeight': {
      immediate: true,
      handler(value) {
        document.documentElement.style.setProperty('--winHeight', value);
      }
    },
    '$route.path'(newVal) {
      if (newVal.includes('Film')) {
        this.currentPage = "Film";
      } else if (newVal.includes('Cinema')) {
        this.currentPage = "Cinema";
      } else if (newVal.includes('Information')) {
        this.currentPage = "Information";
      } else if (newVal.includes('Mypage')) {
        this.currentPage = "Mypage";
      }
    }
  }
}
</script>

<style>
* {
  padding: 0;
  margin: 0;
  text-decoration: none;
  color: black;
}

:root {
  --winWidth: 0;
  --winHeight: 0;
  --showLoading: none;
}

nav {
  position: fixed;
  bottom: 0;
}

nav>ul {
  width: 100vw;
  padding-top: 15px;
  background-color: white;
  list-style: none;
  display: flex;
  justify-content: space-around;
}

nav>ul>li>a {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-decoration: none;
}
</style>