<template>
  <main class="module-ongoing-main">
    <moduleUlLi v-for="item in showList.beAboutToFilmsList" :key="item">
      <template v-slot:box-left>
        <img :src="item.poster" alt="item.poster" width="66px">
      </template>
      <template v-slot:box-center>
        <h4>{{ item.name }} <span class=""></span></h4>
        <p class="filmTexts">&nbsp;</p>
        <p class="filmTexts">主演：
          <span v-for="itemT in item.actors" v-bind:key="itemT">{{ itemT.name }}
            &nbsp;&nbsp;
          </span>
        </p>
        <p class="filmTexts">上映日期：{{ Date(item.premiereAt) }}</p>
      </template>
      <template v-slot:box-reight>
        <button class="buyTicketButton" v-show="item.isPresale">预购</button>
      </template>
    </moduleUlLi>
  </main>
</template>

<script>
import moduleUlLi from '@/components/public_module/module-ul-li.vue';

export default {
  components: { moduleUlLi },
  props: {
    showList: {
      typeof: Array,
      default: []
    }
  },
  mounted() {
    if (document.querySelector(".module-ongoing-main")) {
      document.querySelector(".module-ongoing-main").addEventListener("scroll", (e) => {
        if (e.target.scrollTop + e.target.clientHeight === e.target.scrollHeight) {
          this.$emit("requestData", "beAboutToFilmsList");
        }
      });
    }
  },
}
</script>

<style scoped>
main {
  overflow-x: auto;
  height: calc(var(--winHeight)*1px - 114px);
  scrollbar-width: none;
}

.filmTexts {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  width: calc(var(--winWidth)*1px - 216px);
}

.buyTicketButton {
  width: 50px;
  height: 25px;
  border: 1px solid #ffb232;
  border-radius: 4px;
  top: -50%;
  right: -50%;
  bottom: -50%;
  left: -50%;
  color: #ffb232;
  font-size: 13px;
  text-align: center;
  line-height: 25px;
  background-color: white;
}

.buyTicketButton:hover {
  cursor: pointer;
}
</style>