<template>
  <div>
    <header>
      <RouterLink :to="{ name: 'ongoing' }">
        <span class="headerTitle ongoing"
          v-bind:style="currentBrowse === 'ongoing' ? { color: '#ff5f16', borderBottom: '2px solid #ff5f16' } : {}">正在热映</span>
      </RouterLink>
      <RouterLink :to="{ name: 'beAboutTo' }">
        <span class="headerTitle beAboutTo"
          v-bind:style="currentBrowse === 'beAboutTo' ? { color: '#ff5f16', borderBottom: '2px solid #ff5f16' } : {}">即将上映</span>
      </RouterLink>
    </header>
    <RouterView :showList="showList" @requestData="requestData" />
  </div>
</template>

<script>
// javascript
import { loadXMLDoc } from '@/static/js/feedbackS';
// JSON
import { requestInfo } from '@/static/JSON/request-info.json';

export default {
  created() {// 创建后
    this.requestData("filmsList");
    this.requestData("beAboutToFilmsList");
  },
  data() {
    return {
      currentBrowse: "ongoing",
      requestGetInfo: {
        filmsList: [],
        beAboutToFilmsList: []
      },
      showList: {
        filmsList: [],
        beAboutToFilmsList: []
      },
    }
  },
  methods: {
    requestData(type) {
      switch (type) {
        case "filmsList":
          document.documentElement.style.setProperty('--showLoading', 'flex');
          if (this.requestGetInfo.filmsList.length >= requestInfo.filmsList.data.length) {
            setTimeout("document.documentElement.style.setProperty('--showLoading', 'none')", 1500);
            alert("没有数据了!!!");
            break;
          }
          loadXMLDoc(requestInfo.filmsList.data[this.requestGetInfo.filmsList.length], false, (error, data) => {
            if (error) {
              console.log(error);
            } else {
              setTimeout(() => {
                this.requestGetInfo.filmsList.push(JSON.parse(data));
                document.documentElement.style.setProperty('--showLoading', 'none');
              }, 1000);
            }
          })
          break;
        case "beAboutToFilmsList":
          document.documentElement.style.setProperty('--showLoading', 'flex');
          if (this.requestGetInfo.beAboutToFilmsList.length >= requestInfo.beAboutToFilmsList.data.length) {
            setTimeout("document.documentElement.style.setProperty('--showLoading', 'none')", 1500);
            alert("没有数据了!!!");
            break;
          }
          loadXMLDoc(requestInfo.beAboutToFilmsList.data[this.requestGetInfo.beAboutToFilmsList.length], false, (error, data) => {
            if (error) {
              console.log(error);
            } else {
              setTimeout(() => {
                this.requestGetInfo.beAboutToFilmsList.push(JSON.parse(data));
                document.documentElement.style.setProperty('--showLoading', 'none');
              }, 1000);
            }
          })
          break;
      }
    }
  },
  watch: {
    'requestGetInfo.filmsList': {
      deep: true,
      handler(value) {
        this.showList.filmsList.push(...value[value.length - 1].data.films);
      }
    },
    'requestGetInfo.beAboutToFilmsList': {
      deep: true,
      handler(value) {
        this.showList.beAboutToFilmsList.push(...value[value.length - 1].data.films);
      }
    },
    '$route.path': {
      immediate: true,
      handler(newVal) {
        if (newVal.includes('ongoing')) {
          this.currentBrowse = "ongoing";
        } else if (newVal.includes('beAboutTo')) {
          this.currentBrowse = "beAboutTo";
        }
      }
    }
  }
}
</script>

<style scoped>
header {
  height: 44px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.headerTitle {
  font-size: 24px;
  text-align: center;
  line-height: 42px;
}
</style>