/**
 * 发送 XMLHttpRequest 请求
 * @param {Object} requestInfo - 请求配置
 * @param {string} requestInfo.method - http方法
 * @param {string} requestInfo.url - 请求路径
 * @param {Object} requestInfo.reqHeader - 请求头配置
 * @param {string} requestInfo.reqHeader.key - 请求头的键
 * @param {string} requestInfo.reqHeader.value - 请求头的值
 * @param {boolean} isTrue - 是否异步
 * @param {Function} callback - 回调函数(参数1：error,参数2:data)
 */
export function loadXMLDoc(requestInfo, isTrue = false, callback) {
  let xhr = new XMLHttpRequest();
  xhr.open(requestInfo.method, requestInfo.url, isTrue);
  xhr.setRequestHeader(requestInfo.reqHeader.key, requestInfo.reqHeader.value);
  xhr.onload = function () {
    if (xhr.status >= 200 && xhr.status < 300) {
      callback(null, xhr.response);
    } else {
      callback(new Error(`请求失败，状态码：${xhr.status}`));
    }
  };
  xhr.onerror = function () {
    callback(new Error("网络错误"));
  };
  xhr.send();
}
